import React from "react"
import Layout from "../components/layout"
import { Link, graphql } from "gatsby"
import { Container } from "react-bootstrap"

const IndexPage = ({ data }) => (
  <div>
    <Layout>
      <Container>
        <div class="my-5 p-5 bg-white rounded shadow-sm">
          <h6 class="border-bottom pb-2 mb-0">Recent updates</h6>
          {data.updateDoneList.edges.map(({node}) => (
            <div key={node.id} class="d-flex text-muted pt-3">
              <p class="pb-3 mb-0 small lh-sm border-bottom">
              <strong class="d-block text-gray-dark">@{node.frontmatter.date}</strong>
              <Link to={node.fields.slug}>{node.frontmatter.title}</Link>
              </p>                    
            </div>
          ))}
          <small class="d-block text-end mt-3">
            <Link to="#">All updates</Link>
          </small>
        </div>
      </Container>
    </Layout>
  </div>
)

export default IndexPage

export const pageQuery = graphql`
query {
  updateDoneList: allMarkdownRemark(sort: {fields: frontmatter___date, order: DESC}) {
    edges {
      node {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
        }
      }
    }
  }
}`